@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.landingText {
    margin-top: 30%;
}

.header-color {
    background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.initial {
    background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
    text-align: center;
    height: 900px;
}

.navbar-dark .navbar-nav .nav-link{
  color: white;
}

.navbar-dark .navbar-nav .nav-link:hover{
  color: white;
}

.navbar-dark .navbar-nav .nav-link:visited {
    color: white;
}

.navbar-brand {
    color: white;
}

a:hover {
    text-decoration: none;

}

hr {
  height: 2px;
}

.titleHr {
    width: 30%;
    background-color: rgb(242, 71, 89);
}

.landingHr {
    background-color: white;
    width: 40%;
}

.bgLight {
    background-color: rgb(221, 221, 221);
}


.bgDark {
    background-color: rgb(238, 238, 238);
}

.aTab {
    background-image: url(emiliod.me/static/media/android_logo.0fd25911.png);
    background-position: center;
}

.wTab {
    background-image: url(emiliod.me/static/media/web.6440ea71.png);
    background-position: center;
}

.pTab {
    background-image: url(emiliod.me/static/media/python.4836518a.png);
    background-position: center;
    background-repeat: no-repeat;
}


